import React from "react";
import logoPath from "../images/Vector.svg";
import avatarPath from "../images/avatar.jpg";
import Header from "./Header";
import Main from "./Main";
import Footer from "./Footer";
import PopupWithForm from "./PopupWithForm";
import ImagePopup from "./ImagePopup";
import api from "../utils/Api";
import renderError from "../utils/utils";
import {CurrentUserContext, user} from "../contexts/currentUserContext";

function App() {
  const [isEditProfilePopupOpen, setEditState] = React.useState(false);
  const [isAddPlacePopupOpen, setAddState] = React.useState(false);
  const [isEditAvatarPopupOpen, setAvatarState] = React.useState(false);
  const [selectedCard, setSelectedCard] = React.useState({});
  const [currentUser,setCurrentUser]=React.useState({});

  const handleEditProfile = () => {
    setEditState(true);
  };
  const handleEditAvatarClick = () => {
    setAvatarState(true);
  };
  const handleAddPlaceClick = () => {
    setAddState(true);
  };
  const handleCardClick = (card) => {
    setSelectedCard(card);
  };

  const closeAllPopups = () => {
    setEditState(false);
    setAvatarState(false);
    setAddState(false);
    setSelectedCard({});
  };
 

  React.useEffect(() => {
    
    api
      .getUserData("users/me")
      .then((data) => {
        const {name, about, avatar, _id: id}=data;
        setCurrentUser({name, about, avatar, id});
           
      })
      .catch((error) => {
        renderError(error);
      });
  }, []);
 
  return (
    <div>
      <CurrentUserContext.Provider value={currentUser}>
      
      <PopupWithForm
        name="edit"
        titleForm="Редактировать профиль"
        children={
          <>
            <input
              className="popup__text"
              type="text"
              name="prof-title"
              required
              minLength="2"
              maxLength="40"
              autoComplete="off"
            />
            <span className="popup__text-error" id="prof-title-error" />
            <input
              className="popup__text"
              type="text"
              name="prof-subtitle"
              required
              minLength="2"
              maxLength="200"
              autoComplete="off"
            />
            <span className="popup__text-error" id="prof-subtitle-error" />
          </>
        }
        titleButton="Сохранить"
        isOpen={isEditProfilePopupOpen}
        modeButton=""
        onClose={closeAllPopups}
      />
      <PopupWithForm
        name="submit"
        titleForm="Вы уверены?"
        children={<></>}
        titleButton="Да"
        isOpen=""
        modeButton="popup__send_type_delete"
        onClose={closeAllPopups}
      />
      <PopupWithForm
        name="new"
        titleForm="Новое место"
        children={
          <>
            <input
              className="popup__text"
              type="text"
              name="mesto"
              placeholder="Название"
              required
              minLength="1"
              maxLength="30"
              autoComplete="off"
            />
            <span className="popup__text-error" id="mesto-error" />
            <input
              className="popup__text"
              type="url"
              name="mesto-url"
              placeholder="Ссылка на картинку"
              required
            />
            <span className="popup__text-error" id="mesto-url-error" />
          </>
        }
        titleButton="Создать"
        isOpen={isAddPlacePopupOpen}
        modeButton=""
        onClose={closeAllPopups}
      />

      <PopupWithForm
        name="avatar"
        titleForm="Обновить аватар"
        children={
          <>
            <input
              className="popup__text"
              type="url"
              name="avatar-url"
              placeholder="Ссылка на аватар"
              required
            />
            <span className="popup__text-error" id="avatar-url-error" />
          </>
        }
        titleButton="Сохранить"
        isOpen={isEditAvatarPopupOpen}
        modeButton=""
        onClose={closeAllPopups}
      />

      <ImagePopup card={selectedCard} onClose={closeAllPopups} />

      <Header src={logoPath} alt="логотип" />
      <Main
        src={avatarPath}
        alt="аватарка"
        title="Жак-Ив Кусто"
        subtitle="Исследователь океана"
        onEditAvatar={handleEditAvatarClick}
        onEditProfile={handleEditProfile}
        onAddPlace={handleAddPlaceClick}
        onCardClick={handleCardClick}
      />
      <Footer />
      </CurrentUserContext.Provider>
    </div>
  );
}

export default App;
