import React from "react";
import { CurrentUserContext } from "../contexts/currentUserContext";

const Card = ({ card, onCardClick }) => {
  const currentUser = React.useContext(CurrentUserContext);
  const isOwn = card.iduser === currentUser.id;
  const cardDeleteButtonClassName = (
    `elements__trash ${isOwn ? '' : 'elements__trash_hidden'}`
  ); 
  function handleClick() {
    onCardClick(card);
  }
  return (
    <li className="elements__item">
      <button className={cardDeleteButtonClassName} type="button" />
      <img
        className="elements__img"
        src={card.link}
        alt={card.name}
        onClick={handleClick}
      />
      <div className="elements__footer">
        <h2 className="elements__title">{card.name}</h2>
        <div className="elements__like">
          <button className="elements__heart" type="button"></button>
          <p className="elements__col">{card.like}</p>
        </div>
      </div>
    </li>
  );
};

export default Card;
