import React from "react";
import { CurrentUserContext } from "../contexts/currentUserContext";
import api from "../utils/Api";
import renderError from "../utils/utils";
import Card from "./Card";

const Main = ({
  src,
  alt,
  title,
  subtitle,
  onEditAvatar,
  onEditProfile,
  onAddPlace,
  onCardClick,
}) => {
  const currentUser = React.useContext(CurrentUserContext);
 /* const [userName, setUserName] = React.useState(title);
  const [userDescription, setUserDescription] = React.useState(subtitle);
  const [userAvatar, setUserAvatar] = React.useState(src);*/
  const [cards, setCards] = React.useState([]);
/*
  React.useEffect(() => {
    
    api
      .getUserData("users/me")
      .then((data) => {
        setUserName(data.name);
        setUserDescription(data.about);
        setUserAvatar(data.avatar);
        
      })
      .catch((error) => {
        renderError(error);
      });
  }, [userName, userDescription, userAvatar]);
*/
  React.useEffect(() => {
    api
      .getAllCards("cards")
      .then((data) => {
        const elem = data.map((item) => ({
          idCard: item._id,
          name: item.name,
          link: item.link,
          like: item.likes.length,
          idUser: item.owner._id,
        }));
        
       setCards(elem);
                
      })
      .catch((error) => {
        renderError(error);
      });
  },[]);

   
  return (
    <main className="main">
      <section className="profile">
        <div className="profile__wrapper-avatar" onClick={onEditAvatar}>
          <img src={currentUser.avatar} alt={alt} className="profile__avatar" />
        </div>

        <div className="profile__intro">
          <h1 className="profile__title">{currentUser.name}</h1>
          <button
            className="profile__edit"
            type="button"
            onClick={onEditProfile}
          />
          <p className="profile__subtitle">{currentUser.about}</p>
        </div>
        <button className="profile__add" type="button" onClick={onAddPlace} />
      </section>

      <section>
        <ul className="elements">
          {cards.map((item) => (
            <Card key={item.idCard} card={item} onCardClick={onCardClick} />
          ))}
        </ul>
      </section>
    </main>
  );
};

export default Main;
