import React from "react";

const PopupWithForm = ({
  name,
  titleForm,
  children,
  titleButton,
  isOpen,
  modeButton,
  onClose,
}) => {
  return (
    <section
      className={
        isOpen
          ? `popup popup_type_${name} popup_opened`
          : `popup popup_type_${name}`
      }
    >
      <form
        action="#"
        className="popup__container"
        name={`popup-${name}`}
        noValidate
      >
        <button className="popup__close" type="button" onClick={onClose} />
        <h2 className="popup__title">{titleForm}</h2>
        {children}
        <button
          className={modeButton ? `popup__send ${modeButton}` : "popup__send"}
          type="submit"
        >
          {titleButton}
        </button>
      </form>
    </section>
  );
};

export default PopupWithForm;
